h1 {
  position: relative;
  display: inline-block;
  font-family: 'Avengeance Heroic Avenger Bold', serif;
  text-transform: uppercase;
  font-size: 35px;
	line-height: 170%;
  letter-spacing: 9px;
  text-shadow: 0 0 3px #fff, 0 0 7px #fff, 0 0 12px #fff,
    0 0 17px $color-lightblue, 0 0 10px $color-lightblue,
    0 0 20px $color-lightblue, 0 0 35px $color-lightblue,
    0 0 55px $color-lightblue;

  span {
    color: $color-pink;
    text-shadow: 0 0 3px #fff, 0 0 7px #fff, 0 0 12px #fff, 0 0 17px $color-pink,
      0 0 10px $color-pink, 0 0 20px $color-pink, 0 0 35px $color-pink,
      0 0 55px $color-pink;
  }

  &::before,
  &::after {
    content: '';
    position: absolute;
    left: -2.5%;
    width: 105%;
    height: 2px;
    background: rgb(0, 204, 255);
    background: linear-gradient(
      90deg,
      rgba(0, 204, 255, 0.01) 0%,
      rgba(0, 204, 255, 1) 30%,
      rgba(0, 204, 255, 1) 70%,
      rgba(0, 204, 255, 0.01) 100%
    );
  }
  &::before {
    top: -34px;
  }
  &::after {
    bottom: -22px;
  }

	@media (min-width: 576px) {
		font-size: 52px;
		letter-spacing: 13px;
	}
}
.content {
  text-align: center;
}
.container {
  margin: 0 auto;
  padding: 0 5px;
  max-width: 1630px;
  // min-width: 320px;

	@media (min-width: 576px) {
		padding: 0 15px;
	}
}
.content__title {
  margin: 45px 0 77px;
	z-index: 1;

	@media (min-width: 576px) {
		margin: 4% 0 7%;
	}
}
.cards {
  position: relative;
  margin-bottom: 5%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 46px;
}
.cards__card {
  position: relative;
	width: 300px;
  height: 522px;
  font-size: 18px;
  line-height: 125%;
  background: $color-blue;

  &::before,
  &::after {
    z-index: -1;
    content: '';
    position: absolute;
    inset: -3px;
  }
  &::before {
    box-shadow: 0 0 0 20px #070c23;
  }
  &::after {
    filter: blur(20px);
  }
  &.male::before,
  &.male::after {
    background: linear-gradient(
      45deg,
      $color-pink,
      $color-blue,
      $color-blue,
      $color-lightblue
    );
  }
  &.female::before,
  &.female::after {
    background: linear-gradient(
      45deg,
      $color-lightblue,
      $color-blue,
      $color-blue,
      $color-pink
    );
  }
  &:hover .cards__card-info {
    opacity: 1;
  }
  &:hover::before,
  &:hover::after {
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-play-state: initial;
  }
  &.male:hover::before,
  &.male:hover::after {
    animation-name: color-corners-2;
  }
  &.female:hover::before,
  &.female:hover::after {
    animation-name: color-corners-1;
  }
}
.cards__card-title {
  font-family: 'Avengeance Heroic Avenger Bold', serif;
  letter-spacing: 3px;
  font-size: 30px;
  line-height: 1;
  margin: 0;
  padding: 11px 0 9px;
  background-color: #070c23;

  .cards__card.female & {
    color: $color-pink;
    text-shadow: 0 0 1px #fff, 0 0 2.4px #fff, 0 0 4px #fff,
      0 0 5.8px $color-pink, 0 0 3.4px $color-pink, 0 0 6.8px $color-pink,
      0 0 11.9px $color-pink, 0 0 18.7px $color-pink;
  }
  .cards__card.male & {
    color: $color-lightblue;
    text-shadow: 0 0 1px #fff, 0 0 2.4px #fff, 0 0 4px #fff,
      0 0 5.8px $color-lightblue, 0 0 3.4px $color-lightblue,
      0 0 6.8px $color-lightblue, 0 0 11.9px $color-lightblue,
      0 0 18.7px $color-lightblue;
  }
}

.cards__card-pic {
  height: 472px;
  background: no-repeat center / cover;
}
.content__hint-message {
  display: none;
  &.show {
		z-index: 5;
    display: block;
    position: absolute;
    box-shadow: 0 0 25px $color-lightblue;
    border: 1px solid $color-lightblue;
    border-radius: 15px;
    padding: 18px 20px 20px 60px;
    top: -86px;
    left: 66px;
    font-size: 20px;
		background-color: $color-blue;

    &::before,
    &::after {
      @include icon;
    }
    &::before {
      width: 25px;
      height: 25px;
      left: 21px;
      background-image: url('data:image/svg+xml;base64,');
    }
    &::after {
      top: 47px;
      left: 188px;
      width: 40px;
      height: 40px;
      background-image: url('data:image/svg+xml;base64,');
    }
  }
}
.cards__card-info {
  opacity: 0;
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 20px 15px;
  background-color: rgba(#fff, 0.7);
  color: $color-blue;
  transition: opacity 0.25s ease-in-out;

	&.show {
		opacity: 1;
	}
}
.cards__card-properties {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.cards__card-properties--2 {
  margin-top: 10px;
  margin-bottom: 25px;
}
.cards__card-gender {
  margin-left: -10px;
  width: 25px;
  height: 25px;
  background: no-repeat center/contain;

  .cards__card.female & {
    background-image: url('data:image/svg+xml;base64,');
  }
  .cards__card.male & {
    background-image: url('data:image/svg+xml;base64,');
  }
}

.cards__card-status {
  text-transform: uppercase;
  text-shadow: -1px -1px 1px rgba(255, 255, 255, 0.1),
    1px 1px 1px rgba(0, 0, 0, 0.5);

  &.deceased {
    color: #000;
  }
  .cards__card.male &.alive {
    color: $color-lightblue;
  }
  .cards__card.female &.alive {
    color: $color-pink;
  }
}

.cards__card-status-birthday,
.cards__card-status-deathday {
  position: relative;
  padding-left: 16px;
  color: $color-blue;

  &::before {
    @include icon;
  }
}
.cards__card-status-birthday {
  margin-left: 10px;
}
.cards__card-status-birthday::before {
  top: 1px;
  left: -1px;
  width: 18px;
  height: 18px;
  background-image: url('data:image/svg+xml;base64,');
}
.cards__card-status-deathday {
  margin-left: 5px;
}
.cards__card-status-deathday::before {
  top: 3px;
  left: 0;
  width: 15px;
  height: 15px;
  background-image: url('data:image/svg+xml;base64,');
}
.cards__card-real-name {
  position: relative;
  display: inline;
  padding-left: 24px;

  &::before {
    @include icon;
    top: 0;
    left: 0;
    width: 21px;
    height: 21px;
    background-image: url('data:image/svg+xml;base64,');
  }

  strong {
    @include font-bold;
  }
}
.cards__card-species {
  position: relative;
  padding-left: 24px;
	text-transform: capitalize;

  &::before {
    @include icon;
    top: 1px;
    left: 0;
    width: 19px;
    height: 19px;
    background-image: url('data:image/svg+xml;base64,');
  }
}
.cards__card-citizenship {
  position: relative;
  padding-left: 19px;
	text-transform: capitalize;

  &::before {
    @include icon;
    top: 3px;
    left: 0;
    width: 15px;
    height: 15px;
    background-image: url('data:image/svg+xml;base64,');
  }
}
.cards__card-actors {
  margin-bottom: 25px;
  position: relative;
  display: inline-flex;
  align-self: center;
  padding-left: 19px;

  &::before {
    @include icon;
    top: 3px;
    left: 0;
    width: 15px;
    height: 15px;
    background-image: url('data:image/svg+xml;base64,');
  }

  strong {
    @include font-bold;
    margin-left: 5px;
  }
}
.cards__card-movies div:not(.cards__card-movies div:first-of-type) {
  @include font-bold;
}
.cards__card-movies-title {
  position: relative;
  display: inline-block;
  &::before {
    @include icon;
    top: 3px;
    left: -23px;
    width: 15px;
    height: 15px;
    background-image: url('data:image/svg+xml;base64,');
  }
}
